@mixin box-border-colour($colour) {
  border-color: $colour;

  &:hover {
    border-color: $colour;
  }
  &:active {
    border-color: $colour;
  }
}

#talisman-inputs {
  width: 13em;

  input[type=text] {
    min-width: 100%;

    &.rarity {
      &-common {
        @include box-border-colour(#aaa);
      }

      &-uncommon {
        @include box-border-colour(#4a4);
      }

      &-rare {
        @include box-border-colour(#44a);
      }

      &-epic {
        @include box-border-colour(#808);
      }

      &-legendary {
        @include box-border-colour(#a80);
      }

      &-mythic {
        @include box-border-colour(#a4a);
      }
    }
  }
}

#armor-inputs {
  width: 13em;

  input[type=text] {
    width: 100%;

    &.stat {
      &-dmg {
        @include box-border-colour(#a44);
      }

      &-strength {
        @include box-border-colour(#a44);
      }

      &-crit-dmg {
        @include box-border-colour(#44a);
      }

      &-atk-spd {
        @include box-border-colour(#aa4);
      }
    }
  }
}